import { INTEGRATIONS } from './integrations.js';
import { mergeInt31Files, handleDecimals, mergeJsonData, mergeInt12FolioRecords, adjustIfNeeded, INT20RULES, toEpoch } from './preprocessorutils.js';
import { writePreprocessOutput } from '../test-utils/fileReaders.js';

// ---------- Utilities ----------
function isObject(v: any): v is Record<string, any> {
  return v && typeof v === 'object' && !Array.isArray(v);
}

function selectAll(dataArr: any[], key: string) {
  return dataArr.filter(p => p?.sox_integration && String(p.sox_integration).toLowerCase() === key);
}


function pickMostRecent(records: any[]): any | undefined {
  if (!Array.isArray(records) || records.length === 0) return undefined;
  let best = records[0];
  let bestEpoch = toEpoch(best?.sox_transaction_timestamp);
  for (let i = 1; i < records.length; i++) {
    const e = toEpoch(records[i]?.sox_transaction_timestamp);
    if (e > bestEpoch) {
      best = records[i];
      bestEpoch = e;
    }
  }
  return best;
}

// ---------- Per-integration preprocessing helpers ----------
type ConfirmationId = { provider?: string; [k: string]: unknown };
export function keepACRS(content: any): any {
  if (typeof content === 'string') {
    content = JSON.parse(content);
  }
  
  if(content?.payload){
    const confirmationIds = content?.payload?.confirmationIds;
    const ids = Array.isArray(confirmationIds) ? (confirmationIds as ConfirmationId[]) : [];
    const filtered = ids.filter(c => c?.provider === 'ACRS');
    content.payload.confirmationIds = filtered;
  }
  return JSON.stringify(content);
}

type PreprocessorFn = (roleRecords: any[], comparisonRecords: any[], srcId?: string, destId?: string) => any;
interface PreprocessorRegistry {
  [key: string]: PreprocessorFn;
  __default__: PreprocessorFn; 
}
//define preprocessors for path
//function modify confirmationID
//keep originalValue in different field
//Then compare

export const INTEGRATION_PREPROCESSORS: PreprocessorRegistry = {
  // pick record with most recent sox_transaction_timestamp
  __default__: (records) => pickMostRecent(records),

  // INT15.*: filter ACRS confirmationIds on the selected record
  [INTEGRATIONS.INT15_1_1.toLowerCase()]: (records, secondaryRecords, srcId) => {
    const selected = pickMostRecent(records) ?? records?.[0];
    if (selected && srcId === INTEGRATIONS.INT15_1_1.toLowerCase()) selected.content = keepACRS(selected.content);
    return selected;
  },
  [INTEGRATIONS.INT15_2_1.toLowerCase()]: (records) => {
    const selected = pickMostRecent(records) ?? records?.[0];
    if (selected) selected.content = keepACRS(selected.content);
    return selected;
  },
  [INTEGRATIONS.INT15_3_1.toLowerCase()]: (records, secondaryRecords, srcId) => {
    const selected = pickMostRecent(records) ?? records?.[0];
    if (selected && srcId ===INTEGRATIONS.INT15_3_1.toLowerCase()) selected.content = keepACRS(selected.content);
    return selected;
  },
  // INT11.*: merge reservationList JSON array, sorted by confirmationNumber.value
  [INTEGRATIONS.INT11.toLowerCase()]: (records) => {
    return mergeJsonData(records, {
      selectPath: 'response.response_body.reservationList',
      sortby: 'confirmationNumber.value'
    });
  },
  [INTEGRATIONS.INT11_2.toLowerCase()]: (records) => {
    return mergeJsonData(records, {
      selectPath: 'response.response_body.reservationList',
      sortby: 'confirmationNumber.value'
    });
  },

  [INTEGRATIONS.INT20.toLowerCase()]: (
    srcRecords,
    destRecords,
    srcId,
    destId
  ) => {
    const srcSelected = pickMostRecent(srcRecords);
    const destSelected = pickMostRecent(destRecords);
    
    // default content is srcSelected (same as your original)
    let content = srcSelected;

    if(srcId === INTEGRATIONS.INT20.toLowerCase() && destId === INTEGRATIONS.INT16.toLowerCase()) {
      const key = `${srcId?.toLowerCase?.() ?? srcId}->${destId?.toLowerCase?.() ?? destId}`;
      const rule = INT20RULES[key];
      if (rule && srcSelected && destSelected) {
        const adjusted = adjustIfNeeded(rule, srcSelected, destSelected);
        if (adjusted) {
          content = adjusted;
        }
      }
    }
    return content;
  },
  // INT26: decimals normalization on the selected record
  [INTEGRATIONS.INT26.toLowerCase()]: (records, secondaryRecords, srcId, destId) => {
    const selected = pickMostRecent(records) ?? records?.[0];
    if(selected && srcId === INTEGRATIONS.INT25.toLowerCase() && destId === INTEGRATIONS.INT26.toLowerCase()) {
      return handleDecimals(selected, INTEGRATIONS.INT26);
    }
    return selected;
  },

  // INT31: merge multiple logs (header + detail); fallback to most recent
  [INTEGRATIONS.INT31.toLowerCase()]: (records) => {
    const data =  mergeInt31Files(records);
    return data;
  }
};

// Helper to apply preprocessors using only dataArr
export function applyIntegrationPreprocessors(
  srcId: string,
  destId: string,
  dataArr?: any[]
): { sourcePayload: any; destinationPayload: any } {
  const sKey = String(srcId).toLowerCase();
  const dKey = String(destId).toLowerCase();
  const arr = Array.isArray(dataArr) ? dataArr : [];
  
  // Split records per role
  const sourceRecords = selectAll(arr, sKey);
  const destRecords = selectAll(arr, dKey);

  // Invoke registry (default most-recent selection is inside the registry)
  const sPre = INTEGRATION_PREPROCESSORS[sKey] ?? INTEGRATION_PREPROCESSORS.__default__;
  const dPre = INTEGRATION_PREPROCESSORS[dKey] ?? INTEGRATION_PREPROCESSORS.__default__;

  const sourcePayload = sPre ? sPre(sourceRecords, destRecords, sKey, dKey) : undefined;
  const destinationPayload = dPre ? dPre(destRecords, sourceRecords, sKey, dKey) : undefined;

  //console.log(`Preprocessed source (${srcId}):`, sourcePayload);
  //console.log(`Preprocessed destination (${destId}):`, destinationPayload);
    // const transactionId = sourcePayload?.sox_transaction_id || destinationPayload?.sox_transaction_id || '';
  // Persist output via test-utils writer (no fs/path usage in this module)
  // try {
  //     const fullPath = writePreprocessOutput({
  //     transactionId: transactionId,
  //     srcId: sKey,
  //     destId: dKey,
  //     sourcePayload,
  //     destinationPayload
  //   });
  //   console.log('Preprocess output written:', fullPath);
  // } catch (err) {
  //   console.error('Failed to write preprocess output:', err);
  // }

  return { sourcePayload, destinationPayload };
}