import { time } from "console";
import { INTEGRATIONS } from "./integrations";
//get header and detail records
const hasValidTs = (ts: any) => {
  const t = new Date(ts);
  return !Number.isNaN(t.getTime());
};


/**
 * Converts many date/time inputs to Unix epoch time in milliseconds.
 * Returns 0 on failure (to match your original behavior).
 *
 * Accepted inputs:
 * - number (seconds or milliseconds)
 * - string with digits (seconds or milliseconds, e.g. "1766177784000" or "1766177784")
 * - ISO strings (UTC or local)
 * - common local formats like "2025-01-18 15:36:24,123"
 * - Date objects
 * - JSON .NET format: "/Date(1766177784000)/"
 */
export function toEpoch(ts: any): number {
  try {
    // 1) Date instance
    if (ts instanceof Date) {
      const t = ts.getTime();
      return Number.isFinite(t) ? t : 0;
    }

    // 2) Handle JSON .NET: /Date(1766177784000)/
    if (typeof ts === "string") {
      const matchJsonNet = ts.match(/\/Date\((\d+)\)\//);
      if (matchJsonNet) {
        const num = Number(matchJsonNet[1]);
        return Number.isFinite(num) ? num : 0;
      }
    }

    // 3) Numeric-like input (number or numeric string)
    if (typeof ts === "number" || (typeof ts === "string" && /^\s*-?\d+(\.\d+)?\s*$/.test(ts))) {
      const num = typeof ts === "number" ? ts : Number(ts.trim());
      if (!Number.isFinite(num)) return 0;

      // Normalize seconds vs milliseconds
      // Heuristic:
      // - If absolute value < 1e12 => treat as seconds, multiply by 1000
      //   (1e12 ms ≈ Sep 2001; most modern epochs in ms are >= 1e12)
      // - Else treat as milliseconds
      const abs = Math.abs(num);
      const ms = abs < 1e12 ? num * 1000 : num;

      // Round to integer milliseconds if decimals were provided
      return Math.round(ms);
    }

    // 4) String parsing fallback (ISO and common local formats)
    if (typeof ts === "string") {
      const raw = ts.trim();

      // Try native parsing first (handles RFC/ISO). Returns ms since epoch or NaN.
      let n = Date.parse(raw);
      if (!Number.isNaN(n)) return n;

      // Try to massage common local formats:
      // - Convert space to 'T' and comma to '.' for fractional seconds
      // - If no timezone present, we can try appending 'Z' to force UTC
      const isoLocal = raw.replace(" ", "T").replace(",", ".");
      const hasTZ = /[zZ]|[+-]\d{2}:?\d{2}$/.test(isoLocal);
      const isoCandidate = hasTZ ? isoLocal : isoLocal + "Z";

      n = Date.parse(isoCandidate);
      if (!Number.isNaN(n)) return n;
    }

    // 5) If all parsing attempts fail:
    return 0;
  } catch {
    return 0;
  }
}


export function getLatestHeaderAndDetailRecords(records: any[]) {
  let headerPayload: any = {};
  let detailPayload: any = {};
  let mainRecord: any = {};
  let recordSuccess = "1";

  let headerTs: number | null = null;
  let detailTs: number | null = null;

  for (const record of records) {
    const timestampStr = record.sox_transaction_timestamp;
    const ts = toEpoch(timestampStr);
    if (!hasValidTs(ts)) continue;
    const raw = record.content ?? record.parsed?.content;
    if (!raw) continue;

    const { payload, success } = safeParse(raw);

    if (payload?.folioTransDetailList) {
      if (!detailTs || ts > detailTs) {
        detailPayload = payload;
        detailTs = ts;
        recordSuccess = success;
      }
    } else {
      if (!headerTs || ts > headerTs) {
        mainRecord = record;
        headerPayload = payload;
        headerTs = ts;
      }
    }
  }

  // Fallback to first record when no header was found
  if (!mainRecord || (isObject(mainRecord) && Object.keys(mainRecord).length === 0)) {
    mainRecord = records?.[0] ?? {};
  }

  return { headerPayload, detailPayload, mainRecord, recordSuccess };
}
 
export function mergeInt31Files(records: any[]) {
  let { headerPayload, detailPayload, mainRecord, recordSuccess } = getLatestHeaderAndDetailRecords(records);
  const mergedPayload = { ...headerPayload, ...detailPayload };
  const content = JSON.stringify({ success: recordSuccess, payload: mergedPayload });
  mainRecord = { ...mainRecord, content };
  return mainRecord;
}

export function safeParse(str: string): any {
  try { return JSON.parse(str); } catch { return {}; }
}

export function adjustValuesForNumberOfDecimals<T = any>(input: T): T {
  // Handle primitives & null
  if (input === null || typeof input !== "object") {
    return input;
  }

  // Handle arrays
  if (Array.isArray(input)) {
    return input.map(item => adjustValuesForNumberOfDecimals(item)) as unknown as T;
  }

  // Handle plain objects
  const result: any = {};

  // First, deep-copy & recurse
  for (const key in input) {
    if (Object.prototype.hasOwnProperty.call(input, key)) {
      result[key] = adjustValuesForNumberOfDecimals((input as any)[key]);
    }
  }

  // Normalize possible casing
  const hasValue = Object.prototype.hasOwnProperty.call(input, "value");
  const decKey =
    Object.prototype.hasOwnProperty.call(input, "numberOfDecimals")
      ? "numberOfDecimals"
      : Object.prototype.hasOwnProperty.call(input, "NumberOfDecimals")
        ? "NumberOfDecimals"
        : null;

  if (hasValue && decKey) {
    const decRaw = (input as any)[decKey];
    const dec = Number(decRaw);
    const valRaw = (input as any).value;
    const val = Number(valRaw);

    // Only convert if both are valid numbers and decimals is non-zero
    if (!Number.isNaN(dec) && dec !== 0 && !Number.isNaN(val)) {
      const divisor = Math.pow(10, dec);
      result.value = val / divisor;

      // OPTIONAL: if you prefer keeping the original type as string with fixed decimals:
      // result.value = (val / divisor).toFixed(dec);
       }
  }

  return result as T;

}

export function handleDecimals(soxData: any, intId: string) {

  // Parse content
  const parsedContent = safeParse(soxData.content);
  if (intId === INTEGRATIONS.INT26) {
    const { request, success, response } = parsedContent;
    request.request_body = adjustValuesForNumberOfDecimals(request.request_body);
    soxData.content = JSON.stringify({ success, response, request });
  } 
  return soxData;
}


type AnyRecord = Record<string, any>;

function isObject(v: any): v is AnyRecord {
  return v && typeof v === 'object' && !Array.isArray(v);
}

function clone<T>(v: T): T {
  return isObject(v) ? JSON.parse(JSON.stringify(v)) : v;
}

export function getByPath(obj: AnyRecord | undefined, path: string | undefined): any {
  if (!obj || !path) return undefined;
  
  return path.split('.').reduce((o, k) => (o ? o[k] : undefined), obj);
}

function setByPath(obj: AnyRecord, path: string, value: any): void {
  const parts = path.split('.');
  let cur = obj;
  for (let i = 0; i < parts.length - 1; i++) {
    const p = parts[i];
    if (!isObject(cur[p])) cur[p] = {};
    cur = cur[p];
  }
  cur[parts[parts.length - 1]] = value;
}

function deepMergeObjects(target: any, source: any): any {
  if (Array.isArray(target) && Array.isArray(source)) {
    return [...target, ...source];
  }
  if (isObject(target) && isObject(source)) {
    const out: AnyRecord = { ...target };
    for (const key of Object.keys(source)) {
      if (key in out) {
        out[key] = deepMergeObjects(out[key], source[key]);
      } else {
        out[key] = clone(source[key]);
      }
    }
    return out;
  }
  return source !== undefined ? clone(source) : clone(target);
}

function parseMaybeRecord(recordOrJson: any): { root: AnyRecord | undefined; recordRef?: AnyRecord } {
  if (!recordOrJson) return { root: undefined };
  if (typeof recordOrJson.content === 'string') {
    try {
      return { root: JSON.parse(recordOrJson.content), recordRef: recordOrJson };
    } catch {
      return { root: undefined, recordRef: recordOrJson };
    }
  }
  if (typeof recordOrJson === 'string') {
    try {
      return { root: JSON.parse(recordOrJson) };
    } catch {
      return { root: undefined };
    }
  }
  if (isObject(recordOrJson)) {
    return { root: recordOrJson };
  }
  return { root: undefined };
}

function sortArrayItems(items: any[], sortby?: string): any[] {
  if (!sortby || !Array.isArray(items)) return items;
  return [...items].sort((a, b) => {
    const av = getByPath(a, sortby);
    const bv = getByPath(b, sortby);
    if (av === bv) return 0;
    if (av === undefined) return 1;
    if (bv === undefined) return -1;
    return av < bv ? -1 : 1;
  });
}

export interface MergeJsonOptions {
  selectPath?: string;
  sortby?: string;
}

/**
 * Define rules per (srcId -> destId) pair.
 * Each rule specifies where to read the confirmation IDs from,
 * and which record should be adjusted when src is longer than dest.
 */
export const INT20RULES: Record<
  string,
  {
    srcPath: string;
    destPath: string;
    adjustTarget: 'src' | 'dest';
  }
> = {
  // INT20 -> INT16: adjust src (INT20) if src ID longer than dest ID
  [`${INTEGRATIONS.INT20.toLowerCase()}->${INTEGRATIONS.INT16.toLowerCase()}`]: {
    srcPath: 'payload.resConfirmationNumber',
    destPath: 'request.request_body.staysDetails.reservationConfirmationNumber',
    adjustTarget: 'src',
  }
};

/**
 * 
 * Handle field path modification for INT20 confirmationId length adjustment
 */

export function adjustInt20ConfirmationId(content: any): any {
    const {resConfirmationNumber} = content.payload;  
    //remove 2 last characters from confirmationId value
    content.payload.resConfirmationNumberOriginal = resConfirmationNumber; //keep original value
    content.payload.resConfirmationNumber = resConfirmationNumber.slice(0, -2); //modify value
    return content;
}


type AdjustTarget = 'src' | 'dest';

export interface Rule {
  srcPath: string;
  destPath: string;
  adjustTarget: AdjustTarget;
}

export const toObject = (content: unknown): Record<string, unknown> | undefined => {
  if (!content) return undefined;
  if (isObject(content)) return content as Record<string, unknown>;
  if (typeof content === 'string') return safeParse(content);
  return undefined;
};

export const serializeBack = (
  selected: { content: unknown },
  obj: Record<string, unknown>
) => {
  selected.content = JSON.stringify(obj);
};

export const adjustIfNeeded = (
  rule: any,
  srcSelected: any,
  destSelected: any
) => {
  const srcObj = toObject(srcSelected?.content);
  const destObj = toObject(destSelected?.content);

  if (!srcObj || !destObj) return null;

  const srcConfId = getByPath(srcObj, rule.srcPath);
  const destConfId = getByPath(destObj, rule.destPath);

  if (typeof srcConfId !== 'string' || typeof destConfId !== 'string') {
    return null; // no change
  }
  const srcLonger = srcConfId.length > destConfId.length;

  // Only adjust when INT20 (the adjust target) has a longer confirmation ID than the other side.
  // This preserves your original intent but applies it consistently.
  if (srcLonger) {
    const adjusted = adjustInt20ConfirmationId(srcObj);
    serializeBack(srcSelected, adjusted);
    return srcSelected;
  }

  return null; // no change
};

/**
 * Merge JSON from multiple inputs.
 */
export function mergeJsonData(records: any[], options: MergeJsonOptions = {}): any {
  const { selectPath, sortby } = options;

  let firstParsedRoot: AnyRecord | undefined;
  let firstRecordRef: AnyRecord | undefined;
  const parsedRoots: AnyRecord[] = [];
  const recordRefs: AnyRecord[] = [];

  const consoleLogArray: string[] = [];
  const perRecordCounts: Array<{ index: number; label: string; count: number }> = [];
  let totalInputCount = 0;

  for (let i = 0; i < (records?.length ?? 0); i++) {
    const rec = records[i];
    const { root, recordRef } = parseMaybeRecord(rec);
    if (root) {
      if (!firstParsedRoot) firstParsedRoot = clone(root);
      parsedRoots.push(root);
      if (selectPath) {
        const arr = getByPath(root, selectPath);
        const count = Array.isArray(arr) ? arr.length : 0;
        totalInputCount += count;
        const label =
          (recordRef?.fileName as string) ||
          (recordRef?.sox_integration as string) ||
          `#${i + 1}`;
        perRecordCounts.push({ index: i, label, count });
        consoleLogArray.push(`[mergeJsonData] Record ${label}: ${count} items at selectPath='${selectPath}'`);
      }
    }
    if (recordRef) recordRefs.push(recordRef);
    if (!firstRecordRef && recordRef) firstRecordRef = recordRef;
  }

  if (parsedRoots.length === 0) {
    return firstRecordRef ? { ...firstRecordRef, content: JSON.stringify({}) } : {};
  }

  let merged: AnyRecord;

  if (selectPath) {
    const base: AnyRecord = firstParsedRoot ? clone(firstParsedRoot) : {};
    const allItems: any[] = [];
    for (const r of parsedRoots) {
      const arr = getByPath(r, selectPath);
      if (Array.isArray(arr)) allItems.push(...arr);
    }
    const sorted = sortArrayItems(allItems, sortby);
    setByPath(base, selectPath, sorted);
    merged = base;
    const transactionId = firstRecordRef?.sox_transaction_id || firstParsedRoot?.sox_transaction_id || '';
    consoleLogArray.push(`[mergeJsonData] Total items for ${transactionId} at selectPath='${selectPath}' across ${parsedRoots.length} record(s): input=${totalInputCount}, merged=${sorted.length}`);
    console.table(consoleLogArray);
  } else {
    merged = parsedRoots.reduce((acc, cur) => deepMergeObjects(acc, cur), {});
  }

  return firstRecordRef ? { ...firstRecordRef, content: JSON.stringify(merged) } : merged;
}

/* -------------------- INT12_1 folio merge -------------------- */
/**
 * Merges folio headers and transaction details by folioId while RETAINING the request/response structure.
 *
 * Input records can be:
 *  - Header pages from endpoint '/folios/fetch-headers/query' with shape:
 *      { success, request:{...}, response:{ http_response_code, response_error_message, response_body:{ data:[ {...header} ] } } }
 *  - Transaction pages from endpoint '/folios/fetch-transactions/query' with similar shape where data contains transaction rows.
 *  - Or raw objects containing a single header/transaction (fallback).
 *
 * Output content shape:
 *  {
 *    success: "1" | "0",
 *    request: <copied from first header page if present, else first record>,
 *    response: {
 *      http_response_code: <from first header page or '200'>,
 *      response_error_message: <from first header page or ''>,
 *      response_body: {
 *        nextId: <from first header page or '0'>,
 *        pagination: <from first header page or null>,
 *        data: [ { ...headerFields, folioTransactionDetails: [<transactions>...] }, ... ]
 *      }
 *    }
 *  }
 */
export function mergeInt12FolioRecords(records: any[]): any {
  const folioMap: Record<string, { header?: any; transactions: any[] }> = {};
  let successFlag = "1";
  let baseRecord: any = records?.[0] || {};

  // Capture request/response scaffolding from the first headers page when available
  let headerReq: any | undefined;
  let headerRespCode: any | undefined;
  let headerRespErr: any | undefined;
  let headerNextId: any | undefined;
  let headerPagination: any | undefined;

  const tryExtractRR = (obj: any) => {
    const req = obj?.request;
    const resp = obj?.response;
    const body = resp?.response_body;
    const data = body?.data;
    const endpoint = req?.request_endpoint as string | undefined;

    const isHeaders =
      typeof endpoint === 'string' &&
      endpoint.toLowerCase().includes('/folios/fetch-headers/query');
    const isTransactions =
      typeof endpoint === 'string' &&
      endpoint.toLowerCase().includes('/folios/fetch-transactions/query');

    return { req, resp, body, data, isHeaders, isTransactions };
  };

  const extractPayload = (rec: any): any => {
    const raw = rec?.content || rec?.parsed?.content || rec;
    if (typeof raw === 'string') {
      return safeParse(raw);
    }
    return raw;
  };

  for (const rec of records ?? []) {
    const obj = extractPayload(rec);
    if (!obj || typeof obj !== 'object') continue;

    // Accumulate success flag
    if (obj.success === "0") successFlag = "0";

    const { req, resp, body, data, isHeaders, isTransactions } = tryExtractRR(obj);

    // If this is a header page, capture RR scaffolding if not set
    if (isHeaders && headerReq === undefined) {
      headerReq = req;
      headerRespCode = resp?.http_response_code ?? "200";
      headerRespErr = resp?.response_error_message ?? "";
      headerNextId = body?.nextId ?? "0";
      headerPagination = body?.pagination ?? null;
    }

    // Page data (headers or transactions) in array form
    if (Array.isArray(data) && (isHeaders || isTransactions)) {
      for (const item of data) {
        const folioId = item?.folioId;
        if (!folioId) continue;
        const bucket = folioMap[folioId] || { header: undefined, transactions: [] };
        if (isHeaders) {
          bucket.header = bucket.header ? { ...bucket.header, ...item } : item;
        } else if (isTransactions) {
          bucket.transactions.push(item);
        }
        folioMap[folioId] = bucket;
      }
      continue;
    }

    // Fallback: single object header/txn without RR envelope
    const folioId = obj?.folioId;
    if (folioId) {
      const isTxn = !!obj?.lineItemNo;
      const bucket = folioMap[folioId] || { header: undefined, transactions: [] };
      if (isTxn) bucket.transactions.push(obj);
      else bucket.header = bucket.header ? { ...bucket.header, ...obj } : obj;
      folioMap[folioId] = bucket;
    }
  }

  // Build merged items (populate folioTransactionDetails on header)
  const mergedData = Object.entries(folioMap).map(([folioId, { header, transactions }]) => {
    const safeHeader = header || { folioId };
    const out = { ...safeHeader };
    // Prefer standard field for details if header already has it; otherwise set it
    const detailsKey = 'folioTransactionDetails';
    const existing = (out as any)[detailsKey];
    (out as any)[detailsKey] = Array.isArray(existing) ? [...existing, ...transactions] : transactions;
    return out;
  });

  // Final RR envelope
  const requestOut = headerReq ?? {
    request_endpoint: 'HTTP POST /folios/fetch-headers/query',
    request_querystring: '',
    request_body: {}
  };

  const responseOut = {
    http_response_code: headerRespCode ?? "200",
    response_error_message: headerRespErr ?? "",
    response_body: {
      nextId: headerNextId ?? "0",
      pagination: headerPagination ?? null,
      data: mergedData
    }
  };

  const contentObj = {
    success: successFlag,
    request: requestOut,
    response: responseOut
  };

  return {
    ...baseRecord,
    content: JSON.stringify(contentObj)
  };
}
