// soxWorkflowHelpers.ts

import { queryExecutionClient } from "@dynatrace-sdk/client-query";
import { businessEventsClient } from "@dynatrace-sdk/client-classic-environment-v2";

// -----------------------------
// Constants
// -----------------------------

export const DQL_MAX_POLLS = 10;
export const DQL_REQUEST_TIMEOUT_MS = 10_000;

// -----------------------------
// DQL helper: execute with polling
// -----------------------------

export async function runDqlWithPolling(
    query: string,
    opts?: { maxPolls?: number; requestTimeoutMs?: number }
) {
    const maxPolls = opts?.maxPolls ?? DQL_MAX_POLLS;
    const requestTimeoutMs = opts?.requestTimeoutMs ?? DQL_REQUEST_TIMEOUT_MS;

    const start = await queryExecutionClient.queryExecute({ body: { query } });

    // Immediate success
    if (start.state === "SUCCEEDED") {
        return start.result;
    }

    // No token, cannot poll
    if (!start.requestToken) {
        throw new Error(
            `DQL did not succeed immediately and no requestToken was returned. State: ${start.state}`
        );
    }

    let lastPoll = start;

    for (let i = 0; i < maxPolls && lastPoll.state === "RUNNING"; i++) {
        lastPoll = await queryExecutionClient.queryPoll({
            requestToken: start.requestToken,
            requestTimeoutMilliseconds: requestTimeoutMs,
        });
    }

    if (lastPoll.state !== "SUCCEEDED") {
        throw new Error(`DQL query did not succeed. Final state: ${lastPoll.state}`);
    }

    return lastPoll.result;
}

export const TIMERANGE_MINS = 15;
export const WORKFLOW_HOURLY_LIMIT = 1000;
export const WF_ALLOWANCE = 100;

function getPreviousDayUtcRange() {
  const now = new Date();

  // Yesterday in UTC (date only)
  const yesterday = new Date(Date.UTC(
    now.getUTCFullYear(),
    now.getUTCMonth(),
    now.getUTCDate() - 1
  ));

  const start = new Date(Date.UTC(
    yesterday.getUTCFullYear(),
    yesterday.getUTCMonth(),
    yesterday.getUTCDate(),
    0, 0, 0, 0
  ));

  const end = new Date(Date.UTC(
    yesterday.getUTCFullYear(),
    yesterday.getUTCMonth(),
    yesterday.getUTCDate(),
    23, 59, 59, 999
  ));

  return {
    start: start.toISOString(),
    end: end.toISOString(),
  };
}

interface SoxWorkflowStateEvent {
  'event.provider': 'sox';
  'event.source': 'WORKFLOW';
  'event.type': 'STATE';
  source: string;
  destination: string;
  lastProcessedSourceTimestamp: string; // ISO timestamp  
  lastProcessedTransactionId: string;
  eventCountForDay: number;
  executionId: string;
}

function createStateEvent(params: {
  source: string;
  destination: string;
  lastProcessedSourceTimestamp: string; // ISO timestamp  
  lastProcessedTransactionId: string;
  eventCountForDay: number;
  executionId: string;
}): SoxWorkflowStateEvent {
  return {
    'event.provider': 'sox',
    'event.source': 'WORKFLOW',
    'event.type': 'STATE',
    ...params,
  };
}

function initializeTime() {
  const now = new Date();
  const from = new Date(now);
  from.setHours(0, 0, 0, 0);
  from.setDate(from.getDate() - 1);
  return from.toISOString();
}

export function getPrevDaySourceCount(prevDayEventCount, sourceId) {
  const match = prevDayEventCount?.find(
    r => r.sox_integration === sourceId
  );
  return match?.count ? Number(match.count) : 0;
}

async function logState(stateObj: SoxWorkflowStateEvent) {
  try {
    await businessEventsClient.ingest({
      body: stateObj,
      type: 'application/json; charset=utf-8',
    });
    console.log('State saved successfully', stateObj);
  } catch (e) {
    console.error('Failed to ingest event', e);
  }
}

export async function getInitializeOrLatestState(
  source: string,
  destination: string,
  executionId: string,
  eventCountForDay: number
): Promise<SoxWorkflowStateEvent> {
  const latestState = await getLatestState(source, destination);

  // Prefer values from latestState, fall back to defaults
  const lastProcessedSourceTimestamp =
    latestState?.lastProcessedSourceTimestamp ?? initializeTime();

  const lastProcessedTransactionId =
    latestState?.lastProcessedTransactionId ?? "";

  // If DQL returns eventCountForDay, use it; otherwise use the value passed in
  const effectiveEventCountForDay =
    latestState?.eventCountForDay ?? eventCountForDay;

  const stateObj = createStateEvent({
    source,
    destination,
    lastProcessedSourceTimestamp: String(lastProcessedSourceTimestamp),
    lastProcessedTransactionId: String(lastProcessedSourceTimestamp),
    eventCountForDay: Number(effectiveEventCountForDay) ?? 0,
    executionId,
  });

  // log state only if its not present, it is logged after end of processing in subsequent workflows.
  if (!latestState) {
    await logState(stateObj);
    console.log("Logging state", stateObj);
  } else {
    console.log("Not logging state, returning existing state", stateObj);
  }

  return stateObj;
}



export async function getPreviousDayEventCount() {
  const { start, end } = getPreviousDayUtcRange();
  const dql = `fetch logs, from: "${start}", to: "${end}"
        | filter dt.system.bucket == "sox_logs"
        | summarize count = count(), by: { sox_integration }
        | sort sox_integration asc`;
  //  console.log("getPreviousDayEventCount", dql);
  try {
    const dqlResult = await runDqlWithPolling(dql);
    const records = dqlResult?.records ?? [];
    return records ?? null;
  } catch (ex) {
    console.log("getPreviousDayEventCount", ex);
  }
}


export async function isWorkflowRunning() {    
  const dql = ` fetch dt.system.events, from: -1h
    | filter event.kind == "WORKFLOW_EVENT"
        and event.provider == "AUTOMATION_ENGINE"
        and event.type == "TASK_EXECUTION"
    | filter dt.automation_engine.state != "RUNNING"
    | filter (
          matchesPhrase(dt.automation_engine.workflow.title, "process_not_match_pair_integration_anomalies")
          or matchesPhrase(dt.automation_engine.workflow.title, "process_pair_integration_anomalies")
          or matchesPhrase(dt.automation_engine.workflow.title, "process_transaction_anomalies")
      )
    | summarize count = count(), by: { dt.automation_engine.workflow.title }
    | summarize max_count = max(count)`;
    
      let count = 0;
      try {
        const dqlResult = await runDqlWithPolling(dql);
        const records = dqlResult?.records ?? [];
        if (records.length > 0) {
            count = Number(records[0]?.count ?? 0);
        }
      } catch (ex) {        
        console.log("getWorkflowExecutionCount", ex);
        console.log("getWorkflowExecutionCount - DQL", dql);        
      }
    
      console.log(`getWorkflowExecutionCount ${count}`);    
      return count;
}


